
import { Options, Vue } from 'vue-class-component'
import {
  UserOutlined,
  LockOutlined,
  GithubOutlined
} from '@ant-design/icons-vue'
import { ValidateErrorEntity } from 'ant-design-vue/es/form/interface'
import axiosUtils from '@/utils/AxiosUtils'
import { R } from '@/types/R'
import pubutils from '@/utils/PubUtils'
import { User } from '@/types/User'

interface UserAuth {
  username: string;
  password: string;
}

@Options({
  components: {
    UserOutlined,
    LockOutlined,
    GithubOutlined
  }
})
export default class Login extends Vue {
  // 表单数据
  formData: UserAuth = {
    username: '',
    password: ''
  }

  // 认证通过登录
  async handleFinish(values: UserAuth) {
    const params = `username=${this.formData.username}&password=${this.formData.password}`
    const result = await axiosUtils.post<R<User>>(
      '/platform/login',
      params,
      {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      }
    )
    if (result.code === 0) {
      this.$router.push('/')
      pubutils.setLocalStorageUser(result.data)
    } else {
      this.$toast(result.msg)
    }
  }

  handleFinishFailed(errors: ValidateErrorEntity<UserAuth>) {
    console.log(errors)
  }
}
